import { useRef, useEffect } from "react";

function useCopyPast(onCopy: VoidFunction, onPast: VoidFunction): void {
  const handleCopyRef = useRef(onCopy);
  handleCopyRef.current = onCopy;

  const handlePastRef = useRef(onPast);
  handlePastRef.current = onPast;

  useEffect(() => {

    const keyPressHandler = (event: KeyboardEvent) => {
      const handleCopy = handleCopyRef.current;
      const handlePast = handlePastRef.current;
      if (!event.ctrlKey) return;
      if (event.code === 'KeyC') {
        handleCopy();
      } else if (event.code === 'KeyV') {
        handlePast();
      }
    }

    document.addEventListener('keydown', keyPressHandler);
    return () => {
      document.removeEventListener('keydown', keyPressHandler);
    }
  }, []);
}

export default useCopyPast;